@media (min-width: 1600px) {}

@media (min-width: 1300px) {
    .container,
    .container-sm,
    .container-md,
    .container-lg,
    .container-xl {
        max-width: 1200px;
    }
}

@media (min-width: 1200px) {}

@media (max-width: 1200px) {
    .container,
    .container-sm,
    .container-md,
    .container-lg {
        max-width: 990px;
    }
    #header-main {
        .electro-navbar .electro-navbar-inner {
            .navbar-form {
                .select-search-field {
                    width: 169px;
                    padding-right: 0;
                }
                .input-group-btn {
                    width: calc(61% - 173px);
                }
            }
        }
        .departments-menu .dropdown-menu-category {
            height: 390px;
            overflow-x: hidden;
            overflow-y: scroll;
        }
    }
    #banner-main .carousel-inner .image {
        height: 390px;
    }
}

@media (max-width: 1024px) {}

@media (max-width: 992px) {
    .row {
        margin: 0;
    }
    #header-main {
        .electro-navbar {
            z-index: 91;
            padding: 0;
            width: 50%;
            left: 50%;
            position: fixed;
            transform: translate(-50%, 0);
            .logo-main {
                a {
                    height: 42px;
                    width: auto;
                    text-align: center;
                    img {
                        height: 100%;
                        width: auto;
                    }
                }
            }
            .align-seft {
                display: none;
            }
        }
        .mb-bar {
            display: block;
            height: 42px;
            background: #E5B538;
            position: fixed;
            top: 0;
            left: 0;
            right: 0;
            z-index: 90;
            .open-menu,
            .close-menu {
                position: relative;
                color: #fff;
                font-size: 28px;
                width: 60px;
                padding: 0 15px;
            }
            .close-menu {
                display: none;
            }
        }
        .bg-main {
            z-index: 91;
            padding: 0 0 15px;
            position: fixed;
            left: -100%;
            width: 50%;
            height: calc(100% - 42px);
            background: #E5B538;
            top: 42px;
            border-top: 1px solid #ffffff;
            transition: 0.3s all;
            &.active {
                left: 0;
                transition: 0.3s all;
            }
            .electro-navbar-inner {
                .col-3 {
                    max-width: 100%;
                    flex: 0 0 100%;
                    margin: 9px 0;
                }
                .col-9 {
                    max-width: 100%;
                    flex: 0 0 100%;
                }
            }
        }
        .departments-menu {
            width: calc(100% - 30px);
            margin: 0 auto;
            a {
                padding: 6px 10px;
                font-size: 14px;
            }
        }
        .menu-active {
            padding: 0 0 15px;
            border-top: 1px solid hsla(0, 0%, 85.1%, 0.5);
            #menu-menu-main {
                display: block;
                .menu-item {
                    margin-left: 0;
                    position: relative;
                    border-bottom: 1px solid hsla(0, 0%, 85.1%, 0.5);
                    a {
                        font-size: 14px;
                        color: #fff;
                        text-transform: uppercase;
                        display: block;
                        padding: 10px 10px 10px 15px;
                        width: calc(100% - 41px);
                    }
                }
            }
        }
    }
    #banner-main {
        margin-top: 42px;
        .container {
            padding: 0;
        }
        .carousel-inner .image {
            height: 52vw;
        }
    }
    #primary .woocommerce ul.products li.product img {
        height: 25vw;
    }
    #banner-small-main .item .image {
        height: 18vw;
    }
    #footer {
        .col-6 {
            margin-bottom: 30px;
        }
    }
}

@media (max-width: 767px) {
    #featured-main .featured {
        margin: 0;
        .item {
            padding: 15px;
            .infor {
                .title {
                    margin-bottom: 0;
                    margin-top: 5px;
                    font-size: 14px;
                }
            }
        }
    }
    .pro-header.row {
        display: block;
        .title {
            width: auto;
            font-size: 18px;
            display: inline-block;
            &:after {
                border-top: 45px solid #E5B538;
                border-right: 20px solid transparent;
            }
        }
        .category-list {
            width: 100%;
            justify-content: flex-start;
            margin-top: 10px;
            li {
                margin-left: 0;
                margin-right: 16px;
                margin-right: 0;
                padding: 3px;
            }
        }
    }
    #primary .woocommerce {
        padding: 15px 15px;
        margin: 0 -15px;
        ul.products {
            li.product {
                width: calc(100% / 3);
                h2 {
                    font-size: 16px;
                    height: 48px;
                }
            }
        }
    }
    #banner-small-main {
        margin-top: 0;
        .item .image {
            height: 26vw;
        }
        .row {
            margin-right: -15px;
            margin-left: -15px;
            justify-content: center;
            .col-sm-6 {
                margin-bottom: 15px;
                &:nth-child(2n) {
                    padding-left: 7.5px;
                }
                &:nth-child(2n+1) {
                    padding-right: 7.5px;
                }
            }
        }
    }
    #news-main .news {
        .image {
            height: 21vw;
        }
        .infor {
            .title {
                margin-top: 4px;
                font-size: 16px;
                height: 48px;
            }
            .date {
                margin-top: 0;
                font-size: 14px;
            }
            .intro {
                margin-top: 5px;
                height: 42px;
                font-size: 14px;
            }
            .one-more {
                margin-top: 5px;
                font-size: 14px;
            }
        }
    }
}

@media (max-width: 576px) {
    body {
        font-size: 14px;
    }
    .wrapper-product {
        margin-top: 15px;
    }
    #header-main .bg-main {
        width: 70%;
        .container {
            padding: 0;
        }
    }
    #featured-main {
        margin-top: 15px;
        .container {
            padding: 0;
        }
        .featured .item {
            img {
                height: 30px;
            }
            width: 50%;
            &:after {
                display: none;
            }
            &:nth-child(2n+1) {
                &:after {
                    right: 0;
                    display: block;
                }
            }
            .infor {
                .intro {
                    font-size: 13px;
                }
            }
        }
    }
    .pro-header.row {
        .title {
            font-size: 16px;
            padding: 5px 15px;
            &:after {
                border-top: 34px solid #E5B538;
                border-right: 10px solid transparent;
                right: -10px;
            }
        }
        .category-list li a {
            padding: 4px 10px;
        }
    }
    #primary .woocommerce {
        ul.products {
            margin-bottom: 0;
            li.product {
                width: calc(100% / 2);
                img {
                    height: 45vw;
                    margin-bottom: 5px;
                }
                h2 {
                    font-size: 14px;
                    height: 42px;
                }
                .price {
                    margin: 0 0 5px;
                    height: 40px;
                    .woocommerce-Price-amount {
                        font-size: 14px;
                    }
                    del .woocommerce-Price-amount {
                        font-size: 13px;
                    }
                }
            }
        }
    }
    #banner-small-main {
        .item .image {
            height: 49vw;
        }
        .row {
            .col-sm-6 {
                &:nth-child(2n) {
                    padding-left: 15px;
                }
                &:nth-child(2n+1) {
                    padding-right: 15px;
                }
            }
        }
    }
    #news-main {
        .news {
            margin-top: 15px;
            .image {
                height: 30vw;
            }
        }
        .more-all {
            margin-top: 15px;
            a {
                padding: 5px 24px;
            }
        }
    }
    #footer {
        margin-top: 15px;
        padding: 15px 0 0;
        .container {
            padding: 0;
        }
        .pages {
            .title {
                margin-bottom: 10px;
                font-size: 16px;
            }
            ul li {
                margin-bottom: 5px;
                font-size: 14px;
            }
        }
        .infor {
            .noti {
                height: 43px;
            }
            .connect {
                margin: 15px 0;
                a {
                    margin-right: 7px;
                    i {
                        font-size: 14px;
                    }
                }
            }
        }
        .col-6 {
            margin-bottom: 15px;
        }
    }
    .owl-carousel .owl-nav button {
        width: 30px;
        height: 30px;
    }
}