html {
    font-size: 10px;
    overflow-x: hidden;
    margin: 0;
    padding: 0;
}

body {
    font-family: fontR;
    font-weight: 400;
    font-size: 16px;
    color: #333;
    line-height: 1.5;
    overflow-x: hidden;
    background: #f9f9f9;
}

.container-cxl {
    max-width: 1280px;
    margin: 0 auto;
    padding: 0 15px;
}

input {
    outline: none;
    border: none;
    background: none;
}

.d-flex {
    flex-wrap: wrap;
    align-items: center;
}

p {
    margin: 0;
}

ul {
    list-style: none;
    margin: 0;
    padding-left: 0;
}

a,
a:hover {
    text-decoration: none;
    transition: 0.3s all;
}

img {
    max-width: 100%;
    height: auto;
    object-fit: contain;
}

.lh-1 {
    line-height: 1;
}

.text-upper {
    text-transform: uppercase;
}

.mr-40 {
    margin-top: 40px;
}

.wrapper-content {
    margin-top: 50px;
}

.wrapper-bg {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

.wrapper-title {
    position: relative;
    img {
        height: 8px;
        margin-bottom: 5px;
    }
    .title-bot {
        line-height: 1.2;
        margin-top: 5px;
    }
}

.one-more {
    a {
        display: inline-block;
        padding: 8px 20px;
        border: 1px solid #cccccc;
        &:hover {
            color: #222222;
            font-family: fontB;
            img {
                left: 5px;
                transition: 0.3s all;
            }
        }
        img {
            position: relative;
            vertical-align: inherit;
            transition: 0.3s all;
            margin-left: 5px;
            left: 0;
            height: 13px;
            width: auto;
        }
    }
}

.menu-main-mobile {
    display: none;
    position: relative;
}

#header-main {
    .mb-bar {
        display: none;
    }
    .logo-main {
        position: relative;
        align-self: center;
        a {
            display: block;
            width: 100%;
            height: 100%;
            img {
                width: 100%;
                object-fit: contain;
                height: auto;
                margin: auto;
            }
        }
    }
    .electro-navbar-fixed {
        position: fixed;
        width: 100%;
        z-index: 99;
        top: 0;
    }
    .electro-navbar {
        padding: 12px 0;
        .electro-navbar-inner {
            display: flex;
            position: relative;
            .navbar-form {
                width: 100%;
                display: flex;
                border-radius: 4px;
                border: 2px solid $pri;
                align-self: center;
                button,
                select,
                input {
                    border: 0;
                    padding: 10px;
                    height: 36px;
                    width: 100%;
                    line-height: 1;
                    font-family: fontR;
                }
                .input-search-field {
                    width: 40%;
                    input {
                        border-top-left-radius: 2.467em;
                        border-bottom-left-radius: 2.467em;
                    }
                }
                .select-search-field {
                    width: 200px;
                    border-right: 2px solid $pri;
                    padding-right: 15px;
                    select {
                        padding: 8px 16px;
                    }
                }
                .input-group-btn {
                    width: calc(61% - 200px);
                    button {
                        color: #ffffff;
                        border-top-right-radius: 0;
                        border-bottom-right-radius: 0;
                        i {
                            margin-right: 10px;
                        }
                    }
                }
            }
            .header-icons {
                display: flex;
                align-self: center;
                .header-icon {
                    margin-right: 25px;
                    padding: 0;
                    position: relative;
                    img {
                        width: 26px;
                        height: 26px;
                        object-fit: contain;
                        margin-right: 10px;
                    }
                    &:last-child {
                        margin-right: 0;
                    }
                    a {
                        i {
                            font-size: 20px;
                        }
                        .cart-items-count {
                            background: #EA4933 !important;
                            color: #fff !important;
                            position: absolute;
                            font-size: 0.75em;
                            line-height: 1.75em;
                            font-weight: 700;
                            width: 1.75em;
                            text-align: center;
                            border-radius: 50%;
                            display: inline-block;
                            bottom: 20px;
                            right: -1px;
                        }
                        .total-price {
                            font-size: 12px;
                        }
                    }
                }
            }
        }
    }
    .departments-menu {
        width: 100%;
        position: relative;
        background: #232222;
        height: 100%;
        a {
            color: #ffffff;
            font-weight: 700;
            font-size: 1em;
            display: flex;
            height: 100%;
            align-items: center;
            padding: 0 calc(1.143em + 8px);
            border-radius: 0.5em;
            span i {
                margin-right: 10px;
                font-size: 14px;
            }
        }
        .dropdown-menu-category {
            position: absolute;
            top: 100%;
            left: 0;
            width: 100%;
            border-bottom-left-radius: 0.5em;
            padding: 0;
            border: 2px solid #E5E5E5;
            border-top: none;
            background-color: #fff;
            margin-top: 0;
            z-index: 20;
            display: none;
            transition: all 0s;
            height: 472px;
            &:hover {
                border-bottom-right-radius: 0;
            }
            li {
                list-style: none;
                padding: 0;
                &:hover>a {
                    background-color: #f5f5f5;
                    font-weight: 700;
                    &:after {
                        color: #000;
                    }
                    .sub-list-cate {
                        width: 100%;
                        z-index: 2;
                        background-color: #fff;
                        height: 101%;
                        border-right: 2px solid #fed700;
                        border-bottom-right-radius: 0.5em;
                        border-bottom: 2px solid #fed700;
                    }
                }
                &:last-child a {
                    border-bottom: none;
                }
                >a.active:after {
                    font-family: fontawesome;
                    border-top: none;
                    border-left: none;
                    border-right: none;
                    width: auto;
                    height: auto;
                    content: "\F105";
                    color: #aeaeae;
                    font-size: 1em;
                    line-height: 1;
                    margin-left: 8px;
                    margin-right: 0;
                    position: absolute;
                    right: 4px;
                    top: 50%;
                    transform: translateY(-50%);
                    margin-top: 2px;
                }
                a {
                    display: block;
                    width: 100%;
                    clear: both;
                    font-weight: 400;
                    line-height: 1.5;
                    text-align: inherit;
                    background: 0 0;
                    border: 0;
                    color: #232222;
                    white-space: normal;
                    padding: 11px 12px;
                    border-bottom: 1px solid #ddd;
                    border-radius: 0;
                    position: relative;
                }
                .sub-list-cate {
                    padding: 0.5em 0 0.5em 5px;
                    position: absolute;
                    text-align: left;
                    left: 99%;
                    top: 0;
                    width: 0;
                    overflow: hidden;
                    transition: all 0s;
                    li {
                        padding: 0;
                    }
                    a {
                        height: 35px;
                        &:last-child {
                            border-bottom: none;
                        }
                    }
                }
            }
        }
    }
    .menu-active {
        padding: 18px 0 18px 125px;
        .menu-menu-main-container {
            vertical-align: middle;
        }
        #menu-menu-main {
            padding: 0;
            display: flex;
            margin: 0;
            .menu-item {
                list-style: none;
                margin-left: 40px;
                &:first-child {
                    margin-left: 0;
                }
                a {
                    font-size: 16px;
                    color: #ffffff;
                    text-decoration: none;
                }
            }
            .menu-item-has-children {
                position: relative;
                padding: 0 15px 0 0;
                &:after {
                    content: "\F107";
                    font-family: fontawesome;
                    line-height: 1;
                    font-weight: 900;
                    color: #aeaeae;
                    font-size: 12px;
                    display: inline-block;
                    top: 5px;
                    right: 0;
                    position: absolute;
                }
                &:hover .sub-menu {
                    visibility: visible;
                    transform: translate(0);
                    opacity: 1;
                }
                .sub-menu {
                    visibility: hidden;
                    transform: translate(10px, 10px);
                    border-radius: 0;
                    border-top: 2px solid #fed700;
                    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.28);
                    min-width: 200px;
                    border-bottom-left-radius: 0.5em;
                    border-bottom-right-radius: 0.5em;
                    position: absolute;
                    opacity: 0;
                    padding: 5px 0;
                    margin: 2px 0 0;
                    transition: all 0.3s;
                    background-color: #fff;
                    li {
                        list-style: none;
                        a {
                            display: block;
                            width: 100%;
                            padding: 0.25rem 1.5rem;
                            clear: both;
                            font-weight: 400;
                            color: #212529;
                            text-align: inherit;
                            white-space: nowrap;
                            background-color: transparent;
                            border: 0;
                            &:hover {
                                background-color: #f8f9fa;
                            }
                        }
                    }
                }
            }
        }
    }
}

#banner-main {
    .carousel-inner {
        .image {
            width: 100%;
            height: 470px;
            img {
                object-fit: cover;
                height: 100%;
                width: 100%;
            }
        }
    }
}

#featured-main {
    margin-top: 24px;
    .featured {
        margin-left: -15px;
        margin-right: -15px;
        .item {
            width: calc(100% / 3);
            padding: 32px 25px;
            position: relative;
            &:last-child {
                &:after {
                    display: none;
                }
            }
            &:after {
                content: '';
                position: absolute;
                width: 2px;
                height: 60%;
                top: 50%;
                transform: translate(0, -50%);
                right: 15px;
                background: #E5E5E5;
            }
            img {
                height: 35px;
                width: auto;
                margin-right: 16px;
            }
            .infor {
                .title {
                    margin-bottom: 8px;
                }
            }
        }
    }
}

.wrapper-product {
    margin-top: 40px;
}

.pro-header {
    border-top: 2px solid $pri;
    flex-wrap: inherit;
    height: 60px;
    .title {
        padding: 9px 24px;
        position: absolute;
        width: auto;
        &:after {
            content: '';
            position: absolute;
            right: -20px;
            top: 0;
            border-top: 60px solid $pri;
            border-right: 20px solid transparent;
        }
    }
    .category-list {
        width: 100%;
        justify-content: flex-end;
        li {
            margin-left: 16px;
            a {
                padding: 4px 16px;
                display: block;
                color: #232222;
                border: 1px solid #E5E5E5;
                border-radius: 4px;
                background: #ffffff;
                transition: 0.3s all;
                &:hover {
                    background: #E5B538;
                    color: #ffffff;
                }
                &.active {
                    background: #E5B538;
                    color: #ffffff;
                    &:hover {
                        background: #ffffff;
                        color: #E5B538;
                    }
                }
            }
        }
    }
}

#footer {
    margin-top: 60px;
    padding: 60px 0;
    .infor {
        .logo {
            width: 179px;
            height: auto;
            object-fit: contain;
        }
        .connect {
            margin: 24px 0;
            a {
                display: block;
                border: 1px solid #fff;
                width: 24px;
                height: 24px;
                border-radius: 50%;
                text-align: center;
                margin-right: 24px;
                transition: 0.3s all;
                &:hover {
                    border: 1px solid $pri;
                }
                i {
                    color: #ffffff;
                    font-size: 18px;
                }
            }
        }
        .noti {
            height: 67px;
            width: auto;
            object-fit: contain;
        }
    }
    .pages {
        .title {
            margin-bottom: 24px;
        }
        ul {
            li {
                margin-bottom: 15px;
                a {
                    position: relative;
                    transition: 0.3s all;
                    left: 0;
                    color: #ffffff;
                    &:hover {
                        left: 5px;
                        transition: 0.3s all;
                        color: $pri;
                    }
                }
                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
    }
}

#trademark-main {
    .owl-trademark {
        margin-top: 40px;
        .item {
            height: 115px;
            width: 100%;
            img {
                width: 100%;
                height: 100%;
                object-fit: contain;
            }
        }
    }
}

#banner-small-main {
    margin-top: 60px;
    .row {
        margin-right: -30px;
        margin-left: -30px;
    }
    .item {
        .image {
            position: relative;
            height: 216px;
            width: 100%;
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
        .infor {
            padding: 0 15px;
            position: absolute;
            top: 50%;
            transform: translate(0, -50%);
        }
    }
}

#news-main {
    margin-top: 60px;
    .news {
        margin-top: 40px;
        .image {
            height: 186px;
            width: 100%;
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
        .infor {
            .title {
                margin-top: 15px;
                height: 54px;
                line-height: 1.5;
                overflow: hidden;
                text-overflow: ellipsis;
                -webkit-line-clamp: 2;
                display: -webkit-box;
                -webkit-box-orient: vertical;
            }
            .date {
                color: #6D757C;
                margin-top: 15px;
                line-height: 1.2;
                i {
                    font-size: 15px;
                    margin-right: 11px;
                }
            }
            .intro {
                margin-top: 15px;
                color: #6D757C;
                overflow: hidden;
                text-overflow: ellipsis;
                -webkit-line-clamp: 2;
                display: -webkit-box;
                -webkit-box-orient: vertical;
                height: 48px;
            }
            .one-more {
                margin-top: 15px;
                display: block;
                &:hover {
                    color: $pri;
                    i {
                        left: 5px;
                        transition: 0.3s all;
                    }
                }
                i {
                    position: relative;
                    margin-left: 10px;
                    left: 0;
                    font-size: 15px;
                    transition: 0.3s all;
                }
            }
        }
    }
    .more-all {
        margin-top: 40px;
        a {
            display: inline-block;
            padding: 8px 138px;
            border-radius: 4px;
        }
    }
}

//Product woocommerce
#primary {
    .woocommerce {
        margin: 40px -15px;
        ul.products {
            border: 1px solid #E5E5E5;
            li.product {
                margin: 0;
                width: 25%;
                background: #ffffff;
                border: 1px solid #E5E5E5;
                overflow: hidden;
                &:hover {
                    img {
                        transform: scale(1.1);
                        transition: 0.5s all;
                    }
                }
                &:last-child {
                    margin-right: 0;
                }
                .onsale {
                    display: none;
                }
                img {
                    transition: 0.5s all;
                    height: 300px;
                    width: 100%;
                    object-fit: contain;
                }
                h2 {
                    font-family: fontM;
                    padding: 0 15px;
                    color: #232222;
                    font-size: 18px;
                    line-height: 1.5;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    -webkit-line-clamp: 2;
                    display: -webkit-box;
                    -webkit-box-orient: vertical;
                    height: 54px;
                }
                .price {
                    display: block;
                    padding: 0 15px;
                    margin-top: 12px;
                    margin-bottom: 16px;
                    position: relative;
                    height: 52px;
                    .woocommerce-Price-amount {
                        color: $pri;
                        font-size: 18px;
                        font-family: fontM;
                    }
                    del {
                        position: absolute;
                        bottom: 0;
                        .woocommerce-Price-amount {
                            font-size: 16px;
                            color: #232222;
                            font-family: fontR;
                        }
                    }
                }
            }
        }
    }
}

.owl-carousel {
    .owl-nav {
        text-align: center;
        margin-top: 10px;
        button {
            width: 40px;
            height: 40px;
            border-radius: 5px;
            background: $pri !important;
            color: #ffffff !important;
            display: inline-block;
            margin: 0 5px;
            i {
                font-size: 16px;
            }
        }
    }
}